.active {
  background: linear-gradient(
    119deg,
    rgba(5, 79, 215, 1) 0%,
    rgba(196, 196, 255, 1) 36%,
    rgba(5, 79, 215, 1) 100%
  );
  color: white !important;
}
.active img,
.active svg {
  filter: brightness(5);
}

.navbar {
  background-color: white;
  width: fit-content;
  min-width: 200px;
  height: 100vh;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: 1px solid rgba(128, 128, 128, 0.502);
}

.navbar nav {
  display: flex;
  flex-direction: column;
}
.navbar a {
  text-decoration: none;
  color: black;
  padding: 10px;
  border-radius: 7px;
  display: flex;
  align-items: center;
  margin: 3px 0;
}

.navbar svg {
  width: 20px;
  height: auto;
  color: rgb(80, 79, 79);
  margin-right: 5px;
  margin-left: 2px;
}
.navbar nav img {
  margin-right: 3px;
}
.navbar-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 5px;

  border-top: 2px solid rgb(168, 167, 167);
}
.first-name{
  display: flex;
  align-items: center;
  transition: 0.1s ease-in;
}
.first-name:hover{
  cursor: pointer;
  filter: brightness(1.2);
}

.navbar-footer .first {
  color: rgb(32, 27, 168);
  font-weight: 900;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 2rem;
  padding-right: 2px;
}
.navbar .name {
  font-size: 0.9rem;
}
.navbar .name :last-child {
  font-size: 0.9rem;
  color: grey;
}
.navbar-footer img:hover {
  filter: brightness(0.3);
  cursor: pointer;
}
